@import "../../global.scss";


// warm black color
.navbar{
    width: 100%;
    height: 70px;
    background-color: $mainColor;
    color: $textColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;
    
    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
      justify-content: space-between;


      .left {
          display: flex;
          align-items: center;

          .logo{
              font-size: 30px;
              font-weight: 700;
              text-decoration: none;
              color: inherit;
              margin-right: 40px;
              font-family: Georgia, 'Times New Roman', Times, serif;
            }

            .itemContainer {
                display: flex;
                align-items: center;
                margin-left: 30px;

                @include mobile {
                    display:none;
                }

                .icon {
                    font-size: 30px;
                    margin-right: 5px;
                }

            }
        }

        .right {

            .hamburger {
                width: 31px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                
                span {
                    width: 100%;
                    height: 3px;
                    background-color: $textColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    //styling for when the menu opens to change color
    &.active {
        background-color: $textColor;
        color: $mainColor;

        .hamburger {
            span {
                &:first-child {
                    background-color: $mainColor;
                    transform: rotate(45deg);
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:last-child {
                    background-color: $mainColor;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}